import React, { Component } from "react";
import { BootstrapTable, TableHeaderColumn } from "react-bootstrap-table";
import "react-bootstrap-table/dist/react-bootstrap-table.min.css";
class InActiveStateTable extends Component {
  render() {
    const sortingOptions = {
      defaultSortName: "StateName",
      noDataText: "No records found for inactive state",
      defaultSortOrder: "asc",
      sizePerPageList: [
        {
          text: "5",
          value: 5
        },
        {
          text: "10",
          value: 10
        },
        {
          text: "20",
          value: 20
        },
        {
          text: "All",
          value: this.props.states.length
        }
      ],
      sizePerPage: 5
    };
    return (
      <BootstrapTable
        ref="table"
        data={this.props.states}
        pagination={this.props.states.length > 0 ? true : false}
        search={true}
        options={sortingOptions}
        hover={true}
        csvFileName="States List"
      >
        <TableHeaderColumn dataField="Id" headerAlign="left" isKey hidden>
          Id
        </TableHeaderColumn>
        <TableHeaderColumn
          dataField="StateName"
          headerAlign="left"
          width="60"
          csvHeader="State Name"
          dataSort={true}
        >
          State Name
        </TableHeaderColumn>
        <TableHeaderColumn
          dataField="StateCode"
          headerAlign="left"
          width="40"
          csvHeader="Code"
          dataSort={true}
        >
          State Code
        </TableHeaderColumn>

        <TableHeaderColumn
          dataField="delete"
          dataFormat={this.props.onActivateState}
          headerAlign="left"
          width="20"
          export={false}
        >
          Activate
        </TableHeaderColumn>
      </BootstrapTable>
    );
  }
}
export default InActiveStateTable;



