import React, { Component } from "react";
import CardLayout from "../../../components/Cards/CardLayout";
import { connect } from "react-redux";
import * as actions from "../../../store/actions";
import { FormGroup, Col, Row } from "reactstrap";
import DropdownSelect from "../../../components/InputElement/Dropdown";
import { Link } from "react-router-dom";
import Loader from "../../../components/Loader/Loader";
import DistrictForm from "./DistrictForm";
import ConfirmModal from "../../../components/Modal/ConfirmModal";
import MessageModel from "../../../components/Modal/MasterModal";
import _ from "lodash";
import * as constants from "../../../constants/StatusConstants";
import ActiveDistrictTable from "./ActiveDistrictTable";
import InActiveDistrictTable from "./InActiveDistrictTable";
import { ToastContainer } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";
import * as Toaster from "../../../constants/Toaster";

class DistrictsList extends Component {
  constructor(props) {
    super(props);
    this.state = {
      selectedDistrict: "",
      modalFlag: false,
      stateToEdit: {},
      loading: false,
      showForm: false,
      modalStatus: false,
      districtToDelete: {},
      tableStatus: true,
      message: "",
      messageModelStatus: false
    };
  }
  componentDidMount() {
    this.props.getDistrictsList();
    let compRef = this;
    setTimeout(() => {
      compRef.setState({
        loading: false
      });
      if (compRef.props.districtMasterError) {
        Toaster.Toaster(
          "Something went wrong !",
          compRef.props.districtMasterError
        );
      }
    }, 2000);
  }
  onDistrictValueChange(value) {
    this.setState({
      selectedDistrict: value.value
    });
  }

  onDeleteDistrict(cell, row) {
    return (
      <Link to={this} onClick={() => this.onDelete(row)}>
        <i className="fa fa-trash" title="Deactivate" />
      </Link>
    );
  }
  onDelete(row) {
    //if condition for checking dependent gram and villages
    //if true  => show message modal
    //if false => show delete confirm modal
    if (row.Active) {
      let grampanchayats = _.filter(this.props.grampanchayatsList, {
        districtId: row.Id
      });
      let villages = _.filter(this.props.villagesList, { districtId: row.Id });
      if (grampanchayats.length > 0 || villages.length > 0) {
        this.setState({
          message: `The selected district has dependent active records for ${grampanchayats.length} grampanchayats and ${villages.length} villages. \n Kindly delete the dependent records first.`
        });
        this.onMessageModalToggle();
      } else {
        this.setState({
          districtToDelete: row
        });
        this.onModalToggle();
      }
    } else {
      this.setState({
        districtToDelete: row
      });
      this.onModalToggle();
    }
  }
  onConfirmDelete() {
    let district = { ...this.state.districtToDelete };
    this.state.tableStatus
      ? (district.Active = false)
      : (district.Active = true);
    this.props.deleteDistrict(district.Id, district);
    this.setState({
      modalStatus: !this.state.modalStatus
    });
  }
  onModalToggle() {
    this.setState({
      modalStatus: !this.state.modalStatus
    });
  }

  onMessageModalToggle() {
    this.setState({ messageModelStatus: !this.state.messageModelStatus });
  }
  onEditDistrict(cell, row) {
    return (
      <Link to={this} onClick={() => this.editDistrict(row)}>
        <i className="fa fa-pencil" title="Edit" />
      </Link>
    );
  }
  editDistrict(row) {
    this.setState({
      editDistrict: row,
      showForm: true
    });
  }
  onStatusChange(value) {
    if (value !== null) {
      this.setState({ tableStatus: value });
    } else {
      this.setState({ tableStatus: true });
    }
  }
  onActivateDistrict(cell, row) {
    return (
      <Link to={this} onClick={() => this.onDelete(row)}>
        <i class="fa fa-check-square-o" aria-hidden="true" title="Activate" />
      </Link>
    );
  }
  render() {
    return this.state.showForm ? (
      <DistrictForm {...this.props} editDistrict={this.state.editDistrict} />
    ) : this.state.loading ? (
      <Loader loading={this.state.loading} />
    ) : (
      <div className="address-tabs-margin">
        <CardLayout
          name="Districts"
          buttonName="Add District"
          buttonLink={this}
          buttonClick={() => {
            this.setState({ showForm: true });
          }}
        >
          <Row className="address-drop-margin">
            <Col xs="12" md="10" />
            <Col md="2">
              <DropdownSelect
                label="Status"
                options={constants.tableStatus}
                value={this.state.tableStatus}
                onChange={this.onStatusChange.bind(this)}
                search={false}
                clear={false}
                simpleValue
              />
            </Col>
          </Row>
          <FormGroup row>
            <Col xs="12">
              {this.state.tableStatus ? (
                <ActiveDistrictTable
                  districts={this.props.districts}
                  onEditDistrict={this.onEditDistrict.bind(this)}
                  onDeleteDistrict={this.onDeleteDistrict.bind(this)}
                />
              ) : (
                <InActiveDistrictTable
                  districts={this.props.inactiveDistrict}
                  onActivateDistrict={this.onActivateDistrict.bind(this)}
                />
              )}
            </Col>
          </FormGroup>
        </CardLayout>
        <ConfirmModal
          isOpen={this.state.modalStatus}
          onModalToggle={this.onModalToggle.bind(this)}
          onConfirmDelete={this.onConfirmDelete.bind(this)}
          title={this.state.tableStatus ? "Deactivate" : "Activate"}
          message={
            this.state.tableStatus
              ? "Are you sure you want to deactivate this district record ?"
              : "Are you sure you want to activate this district record ?"
          }
        />
        <MessageModel
          title="Message"
          openFlag={this.state.messageModelStatus}
          toggleFunction={this.onMessageModalToggle.bind(this)}
          message={this.state.message}
        />
      </div>
    );
  }
}
const mapStateToProps = state => {
  return {
    districtsList: state.districtReducer.districtsList,
    districts: state.districtReducer.districts,
    inactiveDistrict: state.districtReducer.inactiveDistrict,
    districtMasterError: state.districtReducer.districtMasterError,
    grampanchayatsList: state.grampanchayatReducer.grampanchayatsList,
    villagesList: state.villageReducer.villagesList
  };
};

const mapDispatchToProps = dispatch => {
  return {
    getDistrictsList: () => dispatch(actions.getDistrictsList()),
    deleteDistrict: (id, district) =>
      dispatch(actions.deleteDistrict(id, district))
  };
};
export default connect(mapStateToProps, mapDispatchToProps)(DistrictsList);



