import React, { Component } from "react";
import CardLayout from "../../../components/Cards/CardLayout";
import { connect } from "react-redux";
import * as actions from "../../../store/actions";
import { FormGroup, Col, Row } from "reactstrap";
import DropdownSelect from "../../../components/InputElement/Dropdown";
import { Link } from "react-router-dom";
import Loader from "../../../components/Loader/Loader";
import StateForm from "./StateForm";
import ConfirmModal from "../../../components/Modal/ConfirmModal";
import MessageModel from "../../../components/Modal/MasterModal";
import _ from "lodash";
import * as constants from "../../../constants/StatusConstants";
import ActiveStateTable from "./ActiveStateTable";
import InActiveStateTable from "./InActiveStateTable";
import { ToastContainer } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";
import * as Toaster from "../../../constants/Toaster";
class StatesList extends Component {
  constructor(props) {
    super(props);
    this.state = {
      selectedState: "",
      modalFlag: false,
      stateToEdit: {},
      loading: false,
      showForm: false,
      modalStatus: false,
      stateToDelete: {},
      tableStatus: true,
      message: "",
      messageModelStatus: false
    };
  }
  componentDidMount() {
    this.props.getStatesList();
    let compRef = this;
    setTimeout(() => {
      if (compRef.props.stateMasterError) {
        Toaster.Toaster(
          "Something went wrong !",
          compRef.props.stateMasterError
        );
      }
    }, 1000);
  }
  onStateValueChange(value) {
    this.setState({
      selectedState: value.value
    });
  }

  onDeleteState(cell, row) {
    return (
      <Link to={this} onClick={() => this.onDelete(row)}>
        <i className="fa fa-trash" title="Deactivate" />
      </Link>
    );
  }
  onDelete(row) {
    //if condition for checking dependent districts , gram and villages
    //if true  => show message modal
    //if false => show delete confirm modal
    if (row.Active) {
      let districts = _.filter(this.props.districtsList, { stateId: row.Id });
      let grampanchayats = _.filter(this.props.grampanchayatsList, {
        stateId: row.Id
      });
      let villages = _.filter(this.props.villagesList, { stateId: row.Id });
      if (
        districts.length > 0 ||
        grampanchayats.length > 0 ||
        villages.length > 0
      ) {
        this.setState({
          message: `The selected state has dependent active records for ${districts.length} districts, ${grampanchayats.length} grampanchayats and ${villages.length} villages. \n Kindly delete the dependent records first.`
        });
        this.onMessageModalToggle();
      } else {
        this.setState({
          stateToDelete: row
        });
        this.onModalToggle();
      }
    } else {
      this.setState({
        stateToDelete: row
      });
      this.onModalToggle();
    }
  }
  onConfirmDelete() {
    let state = { ...this.state.stateToDelete };
    this.state.tableStatus ? (state.Active = false) : (state.Active = true);
    this.props.deleteState(state.Id, state);
    this.setState({
      modalStatus: !this.state.modalStatus
    });
  }
  onModalToggle() {
    this.setState({
      modalStatus: !this.state.modalStatus
    });
  }
  onMessageModalToggle() {
    this.setState({ messageModelStatus: !this.state.messageModelStatus });
  }
  onEditState(cell, row) {
    return (
      <Link to={this} onClick={() => this.onEdit(row)}>
        <i className="fa fa-pencil" title="Edit" />
      </Link>
    );
  }
  onEdit(row) {
    this.setState({
      editState: row,
      showForm: true
    });
  }
  onStatusChange(value) {
    if (value !== null) {
      this.setState({ tableStatus: value });
    } else {
      this.setState({ tableStatus: true });
    }
  }
  onActivateState(cell, row) {
    return (
      <Link to={this} onClick={() => this.onDelete(row)}>
        <i class="fa fa-check-square-o" aria-hidden="true" title="Activate" />
      </Link>
    );
  }
  render() {
    return this.state.showForm ? (
      <StateForm {...this.props} edit={this.state.editState} />
    ) : this.state.loading ? (
      <Loader loading={this.state.loading} />
    ) : (
      <div className="address-tabs-margin">
        <CardLayout
          name="States"
          buttonName="Add State"
          buttonLink={this}
          buttonClick={() => {
            this.setState({ showForm: true });
          }}
        >
          <Row className="address-drop-margin">
            <Col xs="12" md="10" />
            <Col md="2">
              <DropdownSelect
                label="Status"
                options={constants.tableStatus}
                value={this.state.tableStatus}
                onChange={this.onStatusChange.bind(this)}
                search={false}
                clear={false}
                simpleValue
              />
            </Col>
          </Row>
          <FormGroup row>
            <Col xs="12">
              {this.state.tableStatus ? (
                <ActiveStateTable
                  states={this.props.states}
                  onEditState={this.onEditState.bind(this)}
                  onDeleteState={this.onDeleteState.bind(this)}
                />
              ) : (
                <InActiveStateTable
                  states={this.props.inactiveStates}
                  onActivateState={this.onActivateState.bind(this)}
                />
              )}
            </Col>
          </FormGroup>
          <ConfirmModal
            isOpen={this.state.modalStatus}
            onModalToggle={this.onModalToggle.bind(this)}
            onConfirmDelete={this.onConfirmDelete.bind(this)}
            title={this.state.tableStatus ? "Deactivate" : "Activate"}
            message={
              this.state.tableStatus
                ? "Are you sure you want to deactivate this state record ?"
                : "Are you sure you want to activate this state record ?"
            }
          />
          <MessageModel
            title="Message"
            openFlag={this.state.messageModelStatus}
            toggleFunction={this.onMessageModalToggle.bind(this)}
            message={this.state.message}
          />
        </CardLayout>
      </div>
    );
  }
}
const mapStateToProps = state => {
  return {
    statesList: state.stateReducer.statesList,
    states: state.stateReducer.states,
    inactiveStates: state.stateReducer.inactiveStates,
    stateMasterError: state.stateReducer.stateMasterError,
    districtsList: state.districtReducer.districtsList,
    grampanchayatsList: state.grampanchayatReducer.grampanchayatsList,
    villagesList: state.villageReducer.villagesList
  };
};

const mapDispatchToProps = dispatch => {
  return {
    getStatesList: () => dispatch(actions.getStatesList()),
    deleteState: (id, state) => dispatch(actions.deleteState(id, state))
  };
};
export default connect(mapStateToProps, mapDispatchToProps)(StatesList);



